<style scoped>
.snowflake {
    width: 100%;
    margin: 0 auto;
    height: 1000px;
    background: #133d7c;
    position: relative;
    overflow: hidden;
}
</style>
<template>
    <div id="snowflake" class="snowflake">
    </div>
</template>

<script>
export default {
    name: 'snowflake',
    data() {
        return {
            // 容器
            snowflakeModal: '',
            // 容器宽度
            modalWidth: '',
            // 容器高度
            modalHeight: '',
            // 定时器
            interval: null,
            // 雪球生成总个数
            snowflakeNumAll: 100,
            // 生成需求的间隔
            intervalTime: 1,
            // 一轮雪球下降所需时间
            snowflakeOnceTime: null,
            // 雪球平均每次生成多少个
            everySnowflakeNum: null,
            // 雪球基础半径
            baseRadius: 20,
            // 生成雪球比例
            baseScale: .5,
            // 雪球下降的基础速度 px/s
            baseDownSpeed: 80,
            // 雪球下降的X轴基础偏移量
            baseDownXOffset: 200
        };
    },
    methods: {
        // 确定容器基础信息
        initSnowflakeBaseInfo() {
            // 容器
            this.snowflakeModal = document.getElementById('snowflake')
            // 容器宽度
            this.modalWidth = this.snowflakeModal.clientWidth;
            // 容器高度
            this.modalHeight = this.snowflakeModal.clientHeight;
            this.snowflakeOnceTime = parseInt(this.modalHeight / this.baseDownSpeed);
            this.everySnowflakeNum = parseInt(this.snowflakeNumAll / parseInt(this.snowflakeOnceTime / this.intervalTime));
        },

        // 循环下雪~
        loopInitSnowflake() {
            this.initSnowflake();
            this.interval = setInterval(() => {
                this.initSnowflake();
            }, this.intervalTime * 1000)
        },

        // 生成一组随机雪球
        initSnowflake() {
            // 生成雪球
            for (let index = 0; index < this.everySnowflakeNum; index++) {
                // 创建雪球
                let snowflake = document.createElement('i');
                // 设置雪球基础属性
                snowflake.style.borderRadius = '50%';
                snowflake.style.backgroundColor = '#ffffff';
                snowflake.style.position = 'absolute';
                snowflake.style.top = `-${this.baseRadius*2}px`;
                // 设置雪球大小(随机)
                let baseSize = Math.random();
                snowflake.style.width = baseSize * this.baseRadius + 'px';
                snowflake.style.height = baseSize * this.baseRadius + 'px';
                // 设置雪球透明度
                snowflake.style.opacity = Math.random();
                // 雪球出现的位置(X轴,随机)
                snowflake.style.left = Math.random() * this.modalWidth + 'px';
                this.snowflakeModal.appendChild(snowflake)

                // 确定雪球下降方向偏移量（随机+正负）
                let snowflakeOffset = (Math.round(Math.random()) * 2 - 1) * Math.random() * this.baseDownXOffset

                // 开启3d动画（硬件加速）
                snowflake.style.transformStyle = 'preserve-3d';
                // 设置动画
                snowflake.style.transition = `all ${this.snowflakeOnceTime}s linear`;
                setTimeout(() => {
                    snowflake.style.transform = `translateX(${snowflakeOffset}px) translateY(${this.modalHeight + this.baseRadius * 2}px) translateZ(0)`
                    setTimeout(() => {
                        snowflake.remove();
                    }, this.snowflakeOnceTime * 1000)
                }, 100)
            }
        },
    },
    created() {},
    mounted() {
        this.initSnowflakeBaseInfo();
        this.loopInitSnowflake();
    },
    beforeDestroy() {
        if (this.interval) {
            clearInterval(this.interval)
        }
    }
};
</script>